import React from "react";
import {useNavigate} from "react-router-dom";

const UserInfoCard = ({loggedUserInfo, username, fullname, sex, birthday, email, phoneNumber, address}) => {
    const navigate = useNavigate()

    const navigateToSettings = () => {
        navigate("/settings")
    }

    return (
        <div className="card mb-3" style={{backgroundColor: "#f3f3f3"}}>
            <div className="card-body">
                <div className="row">
                    <div className="col-sm-3">
                        <h6 className="mb-0"> </h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                        {fullname || ""}
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-3">
                        <h6 className="mb-0"></h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                        {!sex ? " " : sex === "male" ? "" : ""}
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-3">
                        <h6 className="mb-0"> </h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                        {birthday || ""}
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-3">
                        <h6 className="mb-0">Email</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                        {email || ""}
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-3">
                        <h6 className="mb-0"></h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                        {phoneNumber || ""}
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-3">
                        <h6 className="mb-0"></h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                        {address || ""}
                    </div>
                </div>
                {
                    loggedUserInfo.username === username
                        ? <div className="row">
                            <div className="col-sm-12" style={{marginTop: "10px"}}>
                                <button className="btn btn-primary" onClick={navigateToSettings}></button>
                            </div>
                        </div>
                        : null
                }

            </div>
        </div>
    )
}

export default UserInfoCard
